*,
*:after,
*::before {
  box-sizing: border-box;
}
h1 {text-align: center; margin: 2rem 0;}
a {text-decoration: none;}
.logo {color: #333;}
#menu {width: 100%; height: 50px; background: #000; position: relative;}
.menu {width: 70%; display: flex; margin-left: auto; margin-right: auto;}
.menu li {width: 25%; text-align: center; line-height: 50px;}
.main {color: #fff; font-size: 0.9rem;}
#menubox {width: 100%; height: 200px; position: absolute; left: 0; top: 50px; display: none;
        background: rgba(0, 0, 0, 0.9); color: #fff; text-align: center; padding-top: 2rem;}